<template>
	<v-dialog width="400px" v-model="modal">
		<v-btn class="warning" slot="activator">Купить</v-btn>
		<v-form ref="form" v-model="valid" lazy-validation>
			<v-card>
				<v-container>
					<v-layout row>
						<v-flex xs12>
							<v-card-title>
								<h1 class="text--primary">Купить?</h1>
							</v-card-title>
						</v-flex>
					</v-layout>
					<v-divider></v-divider>
					<v-layout row>
						<v-flex xs12>

							<v-card-text>
								<v-text-field name="name" label="Ваше имя" type="text" v-model="name" :rules="[v => !!v || 'Это поле обязательно']"></v-text-field>
								<v-text-field name="phone" label="Номер телефона" type="tel" v-model="phone" :rules="[v => !!v || 'Это поле обязательно']"></v-text-field>
								<!-- Hidden Required Fields -->
								<input type="hidden" name="project_name" value="Knives">
								<input type="hidden" name="admin_email" value="gospelwork@yandex.ru">
								<input type="hidden" name="form_subject" value="Заказ">
								<!-- END Hidden Required Fields -->
							</v-card-text>

						</v-flex>
					</v-layout>
					<v-divider></v-divider>
					<v-layout row>
						<v-flex xs12>
							<v-card-actions>
								<v-spacer></v-spacer>
								<v-btn flat @click="onCancel">Закрыть</v-btn>

								<v-btn class="success submit" @click="submit">Отправить</v-btn>
							</v-card-actions>
						</v-flex>
					</v-layout>
				</v-container>
			</v-card>
		</v-form>
	</v-dialog>
</template>

<script>
import * as $ from "jquery";

export default {
  data() {
    return {
      modal: false,
      name: "",
      phone: "",
      valid: true
    };
  },
  methods: {
    onCancel() {
      this.name = "";
      this.phone = "";
      this.modal = false;
    },
    submit() {
      $("form").submit(function() {
        //Change
        var th = $(this);
        $.ajax({
          type: "POST",
          url: "../mail.php", //Change
          data: th.serialize()
        }).done(function() {
          setTimeout(function() {
            // Done Functions
            th.trigger("reset");
          }, 1000);
        });
        console.log("data");
        return false;
      });
      this.modal = false;
    }
  }
};
</script>