<template>
	<v-container>
		<v-layout>
			<v-flex xs12 sm6 offset-md3>
				<v-form v-model="valid" lazy-validation>
					<v-text-field v-model="name" :rules="[v => !!v || 'Это поле обязательно для заполнения']" label="Ваше имя" required></v-text-field>
					<v-text-field v-model="email" :rules="emailRules" label="E-mail" required></v-text-field>
					<v-textarea v-model="message" :rules="[v => !!v || 'Это поле обязательно для заполнения']" label="Сообщение" no-resize></v-textarea>
					<!-- Hidden Required Fields -->
					<input type="hidden" name="project_name" value="Knives">
					<input type="hidden" name="admin_email" value="gospelwork@yandex.ru">
					<input type="hidden" name="form_subject" value="Заказ">
					<v-btn class="success" @click="submit" required>Отправить</v-btn>
				</v-form>
			</v-flex>
		</v-layout>
	</v-container>
</template>

<script>
import * as jQuery from "jquery";
export default {
  data() {
    return {
      valid: false,
      name: "",
      email: "",
      message: "",
      emailRules: [
        v => !!v || "Это поле обязательно для заполнения",
        v => /.+@.+/.test(v) || "Неверный e-mail"
      ]
    };
  },
  methods: {
    submit() {
      let $ = jQuery;
      $("form").submit(function() {
        //Change
        var th = $(this);
        $.ajax({
          type: "POST",
          url: "../mail.php", //Change
          data: th.serialize()
        }).done(function() {
          setTimeout(function() {
            // Done Functions
            th.trigger("reset");
          }, 1000);
        });
        console.log("data");
        return false;
      });
      this.modal = false;
    }
  },
  components: {
    jQuery
  }
};
</script>