import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About'
import Blades from './views/Blades'
import Contacts from './views/Contacts'
import Delivery from './views/Delivery'
import Knives from './views/Knives'
import Knife from './views/Knife'
import Materials from './views/Materials'
import Video from './views/Video'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/blades',
      name: 'blades',
      component: Blades
    },
    {
      path: '/contacts',
      name: 'contacts',
      component: Contacts
    },
    {
      path: '/delivery',
      name: 'delivery',
      component: Delivery
    },
    {
      path: '/knives',
      name: 'knives',
      component: Knives
    },
    {
      path: '/knife/:id',
      name: 'knife',
      component: Knife
    },
    {
      path: '/materials',
      name: 'materials',
      component: Materials
    },
    {
      path: '/video',
      name: 'video',
      component: Video
    },
  ]
})
